
import { mapActions, mapGetters } from "vuex";

import { defineComponent } from "vue";
export default defineComponent({
  data() {
    return {
      sec: 3,
      isShowModal: true,
      isShowLastMessage: false,
      name: "",
      nameValid: true,
      phone: "" as string,
      phoneValid: true,
      address: "",
      addressValid: true,
      email: "",
      emailValid: true,
      creditCardNumber: "",
      cardNumberValid: true,
      creditCardValid: "",
      cardValidValid: true,
      creditCardCvv: "",
      cardCvvValid: true,
      isCanSend: false,
    };
  },
  computed: {
    ...mapGetters("Cart", ["getCartArray"]),
  },
  methods: {
    ...mapActions("Cart", ["clearCart"]),
    closeModal() {
      this.emitter.emit("closeModal");
    },
    checkSenderAbility() {
      this.isCanSend = !!(
        this.nameValid &&
        this.name &&
        this.phoneValid &&
        this.phone &&
        this.addressValid &&
        this.address &&
        this.emailValid &&
        this.email &&
        this.creditCardNumber &&
        this.cardNumberValid &&
        this.creditCardValid &&
        this.cardValidValid &&
        this.creditCardCvv &&
        this.cardCvvValid
      );
    },
    isNameInvalid(): void {
      let matcher: RegExpMatchArray | null = this.name.match(/(\p{L}+)/gu);
      if (matcher) {
        this.nameValid =
          matcher.length >= 2 &&
          matcher.every(
            (el) => el[0].toUpperCase() === el[0] && el.length >= 3
          );
      }
      this.checkSenderAbility();
      //console.log(this.name.match(/\p{L}+/gu)); // сколько слов
      //console.log(this.name.match(/(\p{L}+){5}/gu)); // сколько слов и указываем сколько букв должно быть в слове
      //console.log(this.name.match(/(\p{L}+){5}/gu).length); // сколько слов и указываем сколько букв должно быть в слове
    },
    isPhoneInvalid(): void {
      this.phoneValid = this.phone.match(/^\+(\d{9,})$/g) !== null;
      this.checkSenderAbility();
    },
    isAddressInvalid() {
      let matcher: number | null = (
        this.address.match(/[^\s]{5,}/g) as RegExpMatchArray
      )?.length;
      this.addressValid = matcher >= 3;
      this.checkSenderAbility();
    },
    isEmailInvalid() {
      this.emailValid = !!this.email
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );

      this.checkSenderAbility();
    },
    isNumberValid() {
      this.creditCardNumber = this.creditCardNumber.replace(/[^0-9]/g, "");
      if (this.creditCardNumber.length >= 16) {
        this.creditCardNumber = this.creditCardNumber.substring(0, 16);
        //replace(/(\d)(?=(\d\d\d\d)+([^\d]|$))/g, "$1 ")
        this.cardNumberValid = true;
      }
      if (this.creditCardNumber.length < 16) this.cardNumberValid = false;
      this.checkSenderAbility();
    },
    isCvvValid() {
      this.creditCardCvv = this.creditCardCvv.replace(/[^0-9]/g, "");
      if (this.creditCardCvv.length >= 3) {
        this.creditCardCvv = this.creditCardCvv.substring(0, 3);
        this.cardCvvValid = true;
        this.checkSenderAbility();
      }
      if (this.creditCardCvv.length < 3) this.cardCvvValid = false;
    },
    isValidValid() {
      this.creditCardValid = this.creditCardValid.replace(/[^0-9]/g, "");
      if (
        this.creditCardValid.length >= 4 &&
        !this.creditCardValid.split("").includes("/")
      ) {
        this.creditCardValid = this.creditCardValid.substring(0, 4);
        this.creditCardValid = this.creditCardValid.replace(
          /(\d)(?=(\d{2})+(?!\d))/g,
          "$1/"
        );
      }
      if (
        this.creditCardValid.length < 5 ||
        Number(this.creditCardValid.split("").slice(0, 2).join("")) > 12
      ) {
        this.cardValidValid = false;
      } else {
        this.cardValidValid = true;
      }
      this.checkSenderAbility();
    },
    async submit() {
      if (!this.name && this.nameValid) {
        this.nameValid = false;
      }
      if (!this.phone && this.phoneValid) {
        this.phoneValid = false;
      }
      if (!this.address && this.addressValid) {
        this.addressValid = false;
      }
      if (!this.email && this.emailValid) {
        this.emailValid = false;
      }
      if (!this.creditCardNumber && this.cardNumberValid) {
        this.cardNumberValid = false;
      }
      if (!this.creditCardValid && this.cardValidValid) {
        this.cardValidValid = false;
      }
      if (!this.creditCardCvv && this.cardCvvValid) {
        this.cardCvvValid = false;
      }
      this.checkSenderAbility();
      if (this.isCanSend) {
        await this.clearCart();
        this.isShowModal = false;
        this.isShowLastMessage = true;
        let interval = setInterval(this.timer, 1000);

        setTimeout(() => {
          this.isShowLastMessage = false;
          clearInterval(interval);
          this.emitter.emit("closeModal");
          this.$router.push({ name: "home" });
        }, 3000);
        // надпись товары отправлены и через пару секунд переход на главную
      }
    },
    timer() {
      this.sec = this.sec - 1;
    },
  },
});
