
export interface IPromo {
  name: string;
  promo: string;
  discount: number;
  isAdded: boolean;
  isShow: boolean;
}
import { mapGetters } from "vuex";
import { defineComponent } from "vue";
export default defineComponent({
  name: "CartPay",
  data() {
    return {
      isAppliedPromoCode: false as boolean,
      promoValue: "" as string,
      priceDiscount: 0 as number,
      clearInput: false as boolean,
      promoCodes: [
        {
          name: "Rolling Scopes School",
          promo: "RS",
          discount: 10,
          isAdded: false,
          isShow: false,
        },
        {
          name: "EPAM Systems",
          promo: "EPM",
          discount: 12,
          isAdded: false,
          isShow: false,
        },
        {
          name: "K&K online store",
          promo: "K&K",
          discount: 27,
          isAdded: false,
          isShow: false,
        },
      ] as IPromo[],
    };
  },
  mounted() {
    if (localStorage.getItem("discount")) {
      let data = JSON.parse(localStorage.getItem("discount") || "");
      this.promoCodes.forEach((el: IPromo) => {
        if (data.find((elem: IPromo) => elem.promo === el.promo)) {
          el.isAdded = true;
          this.isAppliedPromoCode = true;
          this.calculateDiscount();
        }
      });
    }
  },
  watch: {
    promoValue() {
      if (this.promoValue) {
        this.changePromoValue(this.promoValue);
      } else {
        this.clearInput = false;
      }
    },
    getCartSum() {
      this.calculateDiscount();
    },
  },
  computed: {
    ...mapGetters("Cart", ["getCartArray", "getCartLength", "getCartSum"]),
  },
  methods: {
    saveDiscount() {
      let data = JSON.stringify(this.promoCodes.filter((el) => el.isAdded));
      localStorage.setItem("discount", data);
    },
    openCheckout() {
      this.emitter.emit("openCheckout");
    },
    clearField() {
      this.promoValue = "";
      this.clearInput = false;
      this.promoCodes.forEach((el) => (el.isShow = false));
    },
    changePromoValue(val: string | boolean) {
      this.promoCodes.forEach((el) => {
        if (el.promo === val && el.isShow === false && el.isAdded === false) {
          el.isShow = true;
          this.clearInput = true;
        } else {
          el.isShow = false;
        }
      });
    },
    promoAdd(val: string | boolean | null) {
      this.promoCodes.find((el) => {
        if (el.promo === val) {
          el.isShow = false;
          el.isAdded = true;
          this.promoValue = "";
          this.isAppliedPromoCode = true;
        }
      });
      this.calculateDiscount();
      this.saveDiscount();
    },
    promoDel(val: string | boolean | null) {
      this.priceDiscount = this.getCartSum;
      this.promoCodes.find((el) => {
        if (el.promo === val) {
          el.isAdded = false;
          el.isShow = false;
        }
      });
      this.calculateDiscount();
      this.isAppliedPromoCode = !this.promoCodes.every(
        (el) => el.isAdded === false
      );
      if (this.priceDiscount === this.getCartSum) {
        this.priceDiscount = 0;
      }
      this.promoValue = "";
      this.saveDiscount();
    },
    calculateDiscount() {
      let acc = 0;
      this.promoCodes.filter((el) => {
        if (el.isAdded) {
          acc += Math.ceil((this.getCartSum / 100) * el.discount);
        }
      });
      this.priceDiscount = this.getCartSum - acc;
      if (this.priceDiscount === this.getCartSum) {
        this.priceDiscount = 0;
      }

      if (this.getCartLength === 0) {
        this.promoCodes.forEach((el) => {
          el.isShow = false;
          el.isAdded = false;
        });
        this.isAppliedPromoCode = false;
      }
    },
  },
});
